import { Injectable } from '@angular/core';

const TOKEN_KEY = 'auth-token';
const USER_KEY = 'auth-user';

@Injectable({
  providedIn: 'root'
})
export class TokenStorageService {
  constructor() { }

  signOut(): void {
    window.sessionStorage.clear();
    window.localStorage.clear();
  }

  public saveToken(token: string,rememberMe:boolean): void {
    if(rememberMe){
      window.localStorage.removeItem(TOKEN_KEY);
      window.localStorage.setItem(TOKEN_KEY, token);
    }
    else
    {
      window.sessionStorage.removeItem(TOKEN_KEY);
      window.sessionStorage.setItem(TOKEN_KEY, token);
    }

  }

  public getToken(): string | null {
     if(window.localStorage.getItem(TOKEN_KEY)===null){
       return window.sessionStorage.getItem(TOKEN_KEY)
     }
     else {
       return window.localStorage.getItem(TOKEN_KEY);
     }
  }

  public saveUser(user: any,rememberMe:boolean): void {
    if(rememberMe){
      window.localStorage.removeItem(USER_KEY);
      window.localStorage.setItem(USER_KEY, JSON.stringify(user));
    }
    else
    {
      window.sessionStorage.removeItem(USER_KEY);
      window.sessionStorage.setItem(USER_KEY, JSON.stringify(user));
    }
  }

  public getUser(): any {
    if(window.sessionStorage.getItem(USER_KEY)){
      return JSON.parse(window.sessionStorage.getItem(USER_KEY));
    }

    if(window.localStorage.getItem(USER_KEY)){
      return JSON.parse(window.localStorage.getItem(USER_KEY));
    }

    return {};
  }
}
